.predic-mvc-clearfix {
  position: relative;
  width: 100%;
  display: block;
  clear: both;
}

.predic-mvc-form-section {
  margin-top: 50px;
  margin-bottom: 50px;

  h1 {
    font-size: 24px;
  }

  h5 {
    color: $color-grey-dark;
    font-size: 16px;
  }
}

form {
  max-width: 600px;
  margin: 30px 10px;
  overflow: auto;
  height: auto;

  .mdl-textfield {
    width: 100%;
    margin-bottom: 20px;
  }

  button {
    float: right;
  }
}

.margin-top-30 {
  margin-top: 30px;
}

main {
  background-image: url('../../images/background-min.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;

  &:before {
    content: '';
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $color-white-085;
    z-index: -1;
  }
}
